@use "mixins";
@use "variables" as var;

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  position: relative;
}

.welcomePage {
  @include mixins.firstPage;

  @include mixins.tablet {
    h1 {
      font-size: 3em;
    }
  }
}

nav {
  @include mixins.nav;
 

  @include mixins.tablet {
    nav {
      justify-content: space-evenly;
    }
    ul {
      justify-content: flex-start;
    }
  }

}

.aboutMe {
  @include mixins.aboutMePage;

  @include mixins.tablet {
    justify-content: flex-start;
  }
}

.projects {
  @include mixins.projectsPage;

  @include mixins.tablet {
    .projects--container {
      // flex-direction: row;
      // flex-wrap: wrap;
      // align-items: center;
      // justify-content: center;
    }

    .project{
      width: 50%;

      img {
        // width: 500px;
        // height: 500px;
      }
    }
  }
}

//typing animationen
.typewriter h2 {
  @include mixins.typeAnimation;
}



@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  45% {
    border-color: rgb(204, 38, 141);
  }
}

.icon {
  position: relative;
}
.fa-cat {
  position: absolute;
  left: -50px;
  color: var.$accent-color;
  font-size: 2em;

  &:hover {
    cursor: pointer;
    transform: scale(1.1);
  }
}